<template>
    <div>
        <mhead-top titleHead="我的发票" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/index/index" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="mu-invoice" v-for="(item,index) in tableData">
                <div class="mui-top flex f-nowrap">
                    <div class="type">发票类型：{{item.type==0?'企业':'个人'}}</div>
                    <div class="name">
                        <router-link to="/userCenter/invoice/add">
                            <el-button size="mini" @click="editInvoice(item.invoice_id?item.invoice_id:'',item.type)">编辑</el-button>
                        </router-link>
                    </div>
                </div>
                <div class="mui-control" v-if="item.invoice_id">
                    <div class="flex f-nowrap j-bet" v-if="item.type==0">
                        <span class="mui-lfTit">发票抬头</span>
                        <span class="tr">{{item.ename}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet" v-if="item.type==0">
                        <span class="mui-lfTit">公司税号</span>
                        <span class="tr">{{item.taxnumber}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet" v-if="item.type==0">
                        <span class="mui-lfTit">开户银行</span>
                        <span class="tr">{{item.bank}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet" v-if="item.type==0">
                        <span class="mui-lfTit">银行账号</span>
                        <span class="tr">{{item.banknumber}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet" v-if="item.type==1">
                        <span class="mui-lfTit">名称</span>
                        <span class="tr">{{item.title}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet">
                        <span class="mui-lfTit">电话</span>
                        <span class="tr">{{item.phone}}</span>
                    </div>
                    <div class="flex f-nowrap j-bet">
                        <span class="mui-lfTit">地址</span>
                        <span class="tr">{{item.address}}</span>
                    </div>
                </div>
                <div class="mui-control color9" v-else>您还尚未有编辑此发票</div>
            </div>
<!--            <div class="mu-addBtn">-->
<!--                <span>-->
<!--                    <el-button type="primary" class="w"><i class="el-icon-plus el-icon&#45;&#45;left"></i>新建发票</el-button>-->
<!--                </span>-->
<!--            </div>-->
        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {invoice} from '../../../service/api'
    export default {
        name: 'mInvoice',
        data(){
            return{
                tableData: [],
                invoiceType:'0',//0是企业，1是个人
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{


        },
        methods:{
            async initData(){
                // 获取表格数据
                var res = await invoice();
                this.login = res.status==12007?false:true;
                if(res){
                    if(res.status != 12007){
                        this.calTable(res);
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            calTable(res){
                var obj = [
                    {
                        invoice_id:'',
                        type:0,
                        title:"",
                        ename:"",
                        taxnumber:"",
                        bank:"",
                        address:"",
                        phone:""
                    },
                    {
                        invoice_id:'',
                        type:1,
                        title:"",
                        ename:"",
                        taxnumber:"",
                        bank:"",
                        address:"",
                        phone:""
                    }
                ];
                for(var i = 0 ; i < res.length ; i++){
                    if(res[i].type == 0){
                        obj[0] = res[i];
                    }else{
                        obj[1] = res[i];
                    }
                }
                this.tableData = obj;
            },
            async editInvoice(invoice_id,type){
                console.log(type);
                this.invoiceType = String(type);
                if(invoice_id){//编辑发票
                    this.$router.push({path:"/userCenter/invoice/add",query:{invoice_id:invoice_id,type:this.invoiceType}});
                }else{
                    //新建发票
                    this.$router.push({path:"/userCenter/invoice/add",query:{invoice_id:invoice_id,type:this.invoiceType}});
                }
            }


        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mu-invoice{
        background-color: #fff;
        padding:0 px2rem(20);
        margin-top:px2rem(20);
    }
    .mui-top{
        justify-content: space-between;
        padding:px2rem(20) 0 px2rem(10) 0;
    }
    .mui-text{
        padding-bottom: px2rem(20);
    }
    .mui-control{
        justify-content: space-between;
        padding:px2rem(20) 0;
        border-top:1px solid $bc;
    }
    .mui-lfTit{
        width:px2rem(200);
        color:#999;
    }
    .mu-addBtn{
        display: flex;
        flex-wrap:nowrap;
        height: px2rem(104);
        line-height: px2rem(104);
        background-color: #fff;
        position: fixed;
        left:0;
        bottom:0;
        width: 100%;
        border-top:1px solid #eee;
    }
    .mu-addBtn span{
        background-color: $default;
        color:#fff;
        width:100%;
        text-align: center;
        font-size: px2rem(32);
    }

</style>



